import { useState } from 'react';
import { Table as ATable, Input, /*Button*/ } from 'antd';
import { CloseCircleTwoTone } from '@ant-design/icons';

import speed from './speed.json';
import speed_mod from './speed_mod.json';
import bilibili from './img/bilibili.png';
import weibo from './img/weibo.png';
import github from './img/github.png';
import logo from './img/logo.png';

import 'antd/dist/antd.min.css';

import styles from './index.module.css';

const App = () => {
  // 
  const handleData = (data) => {
    return data.map((item, index) => {
      // key+1
      if (!item.key) {
        item.key = index + 1;
      }

      // B
      if (item.BID) {
        item.BURL = `https://www.bilibili.com/video/${item.BID}`;
      }

      return item;
    });
  };

  const speedData = handleData(speed);
  const speedDataMod = handleData(speed_mod);

  // state
  const [rankData, setRankData] = useState(speedData);
  const [rankDataMod, setRankDataMod] = useState(speedDataMod);

  // state
  // const [isMod, setIsMod] = useState(false);

  // 
  const [descStatus, setDescStatus] = useState(false);

  // 
  const [pagination, setPagination] = useState(false);

  return (
    <>
      <div className={styles.main}>
        <Title >
          <Input
            addonBefore=""
            placeholder=""
            onChange={(e) => {
              const dataFilter = (data) => data.filter((item) => {
                const reg = new RegExp(e.target.value, 'i');
                return reg.test(item.car);
              });

              setRankData(dataFilter(speedData));
              setRankDataMod(dataFilter(speedDataMod));
            }}
            allowClear
          />

        </Title>

        {/* <div className={styles.switchBox}>
          <Button
            className={styles.switch}
            onClick={() => setIsMod((prevState) => !prevState)}
          >
            {isMod ? '' : ''}
          </Button>

          <Button
            className={styles.pagination}
            onClick={() => setPagination((prevState) => !prevState)}
          >
            <span>{pagination ? '' : ''}</span>
          </Button>
        </div> */}

        {/* {
          isMod
            ? (
              <Table
                rankData={rankDataMod}
                pagination={pagination}
                title={<span></span>}
              />
            )
            : (
              <Table
                rankData={rankData}
                pagination={pagination}
                title={<span></span>}
              />
            )
        } */}

        <Table
          rankData={rankData}
          pagination={pagination}
          title={<span></span>}
        />

        {rankDataMod.length === 0
          ? <></>
          : <Table
            rankData={rankDataMod}
            pagination={pagination}
            title={<span></span>}
          />
        }

        <div className={styles.buttonBox}>
          <button
            className={styles.descButton}
            onClick={() => setDescStatus((prevState) => !prevState)}
          >
            <span>     </span>
          </button>
        </div>

        <Description descStatus={descStatus} setDescStatus={setDescStatus} />
      </div>

      <Footer />
    </>
  );
};

const Table = (
  {
    rankData,
    pagination,
    title
  }
) => {
  const { Column, ColumnGroup } = ATable;

  // 
  const handleSpeed = (speed) => {
    let time = '';
    let minute = Math.floor(speed / 60);
    let second = Math.round((speed - minute * 60) * 100) / 100;

    if (speed < 180) {
      speed % 1 === 0
        ? second < 10
          ? time = `${minute}:0${second}.00`
          : time = `${minute}:${second}.00`
        : second < 10
          ? time = `${minute}:0${second}`
          : time = `${minute}:${second}`;
    } else {
      time = '!';
    }

    return time;
  };

  return (
    <ATable
      title={() => title}
      bordered={true}
      dataSource={rankData}
      size={'small'}
      // sticky={true}
      scroll={{ x: 'max-content' }}
      pagination={pagination}
      onRow={(val) => {
        return {
          className: val.speed < 70
            ? `${styles.kbracer} ${styles.kbracer1}`
            : val.speed < 74
              ? `${styles.kbracer} ${styles.kbracer2}`
              : val.speed < 78
                ? `${styles.kbracer} ${styles.kbracer3}`
                : val.speed < 80
                  ? `${styles.kbracer} ${styles.kbracer4}`
                  : val.speed < 85
                    ? `${styles.kbracer} ${styles.kbracer5}`
                    : `${styles.kbracer} ${styles.kbracer6}`
        }
      }}
    >
      <Column
        title=""
        dataIndex="key"
        key="key"
        align="center"
      />
      <Column
        title=""
        dataIndex="car"
        key="car"
      />
      <Column
        className={styles.highlight}
        title=""
        dataIndex="speed"
        key="speed"
        render={(text) => handleSpeed(text)}
      />
      <Column
        className={styles.highlight}
        title="km/h"
        dataIndex="limit"
        align="center"
        key="limit"
      />
      <Column
        title="0-100s"
        dataIndex="accelerate"
        key="accelerate"
        align="center"
        width="75px"
      />
      <Column
        title=" ()"
        dataIndex="temperature"
        key="temperature"
        align="center"
        width="50px"
      />
      <Column
        title=" (Ps)"
        dataIndex="hp_content"
        key="hp_content"
        align="center"
      />
      <Column
        title=""
        dataIndex="Powertrain"
        key="Powertrain"
      />
      <ColumnGroup title={() => ''}>
        <Column
          title=""
          dataIndex="tyre_type_f"
          key="tyre_type_f"
          onCell={(res) => {
            if (res.tyre_type_f === res.tyre_type_r) {
              return {
                colSpan: 2,
              }
            }
          }}
        />
        <Column
          title=""
          dataIndex="tyre_type_r"
          key="tyre_type_r"
          onCell={(res) => {
            if (res.tyre_type_f === res.tyre_type_r) {
              return {
                colSpan: 0,
              }
            }
          }}
        />
        <Column
          title=""
          dataIndex="tyre_width_f"
          key="tyre_width_f"
          align="center"
          onCell={(res) => {
            if (res.tyre_width_f === res.tyre_width_r) {
              return {
                colSpan: 2,
              }
            }
          }}
        />
        <Column
          title=""
          dataIndex="tyre_width_r"
          key="tyre_width_r"
          align="center"
          onCell={(res) => {
            if (res.tyre_width_f === res.tyre_width_r) {
              return {
                colSpan: 0,
              }
            }
          }}
        />
      </ColumnGroup>
      <Column
        title=""
        dataIndex="Btitle"
        key="Btitle"
        render={(text, record) => record.BURL
          ? <a href={record.BURL}>{text}</a>
          : <span>{text}</span>}
      />
    </ATable>
  )
};

const Title = (props) => {
  return (
    <div className={styles.title}>
      <div className={styles.header}>
        <img src={logo} alt="logo" />
        <div className={styles.desc}>
          <h1></h1>
          <p></p>
        </div>
      </div>
      <div className={styles.search}>
        {props.children}
      </div>
    </div>
  )
};

const Footer = () => {
  return (
    <div className={styles.footer}>
      <div className={styles.left}>
        <div>
          
          <a href="https://space.bilibili.com/49576477">
            <img src={bilibili} alt="bilibili" />
            @
          </a>
          &nbsp;
          &nbsp;
          <a href="https://www.weibo.com/u/5934299797">
            <img src={weibo} alt="weibo" />
            @
          </a>
        </div>

        <div>
          
          <a href="https://kbracer.github.io/">
            <img src={logo} alt="kbracer" />
            
          </a>
        </div>
      </div>

      <div className={styles.right}>
        
        &nbsp;<a href="https://github.com/facebook/react/">React</a>&nbsp;
        
        &nbsp;<a href="https://github.com/ant-design/ant-design/">Ant-Design</a>&nbsp;
        
        <div>
          
          <a href="https://github.com/Phil-Libra">
            Phil_Libra
          </a>
          &nbsp;
          
          <a href="https://github.com/Phil-Libra/kbracer-goldenport">
            <img src={github} alt="github" />
            GitHub
          </a>
        </div>

      </div>
    </div>
  )
};

const Description = (
  {
    descStatus,
    setDescStatus
  }
) => {
  return (
    <>
      <CloseCircleTwoTone
        style={
          {
            display: descStatus ? 'block' : 'none',
            overflow: 'visible',
            position: 'fixed',
            top: '14.5%',
            left: '14.5%',
            fontSize: 20,
            zIndex: 4
          }
        }
        twoToneColor="#aaa"
        onClick={() => setDescStatus((prevState) => !prevState)}
      />
      <div
        className={styles.description}
        style={{ display: descStatus ? 'block' : 'none' }}
      >
        <h3>1.</h3>
        <p>10-25</p>
        <p>200ps30100.X1s</p>
        <p>0</p>
        <p>S55911RS0</p>


        <h3>2.</h3>
        <p>S3</p>

        <h3>3.</h3>
        <p>01</p>
        <p>01100ps112450ps911s992163</p>

        <h3>4.</h3>
        <p>Hot lap</p>
        <p></p>
        <p></p>
      </div>
    </>
  )
};

export default App;
